@import '../../sass/main.scss';

.navbar {
    @include flex;
    width: 300px;
    height: 80px;
    @include flex(row, center, center);
    &_list {
        @include flex(row, space-between, center);
        width: 300px;
        height: 80px;
        padding: 0px;
        &_item {
            @include flex;
            width: 80px;
            height: 100%;
            color: black;
            cursor: pointer;
        }
    }
}
